import { isArray, isObject } from "./utils";
import Dep from "./dep";
/**
 * 
 *
 * @class Observer
 */
export default class Observer {
  private data: any = undefined;
  /**
   *Creates an instance of Observer.
   * @param {*} data []
   * @param {string} path [key()]
   * @memberof Observer
   */
  public constructor(data: any, path: string) {
    if (data.__isProxy === 1) {
      this.data = data;
      return;
    }
    if (isObject(data) || isArray(data)) {
      this.data = this._observe(data, path);
      return;
    }
    this.data = data;
  }
  /**
   * proxy
   *
   * @private
   * @param {Record<string, any>} data
   * @param {string} path
   * @return {*}
   * @memberof Observer
   */
  private _observe(data: Record<string, any>, path: string): any {
    const dep = new Dep(); // 
    const keys = Object.keys(data);

    keys.forEach((key: any): void => {
      const value = data[key];
      data[key] = new Observer(value, `${path}__${key}`).getData();
    });

    const handler = {
      get(target: any, property: string): any {
        if (!target.hasOwnProperty(property) && typeof property === "symbol") {
          return Reflect.get(target, property);
        }

        // watcher
        const curWatcher = Dep.curWatcher;
        if (curWatcher) {
          dep.addDep(`${path}____isProxy`, curWatcher); // __isProxy
          dep.addDep(`${path}__${property}`, curWatcher);
        }
        return Reflect.get(target, property);
      },
      set(target: any, property: string, value: any): boolean {
        let args = {};
        dep.beforeNotfiy(`${path}__${property}`);

        if (isArray(value)) {
          args = { isBuildIntegral: 1 };
        }

        // handler
        if (isArray(target)) {
          args = { ...args, property, value };
          // 
          if (!target[property]) {
            dep.copyArrayWatcherDep(`${path}`, `${path}__${property}`);
          }
        }

        target[property] = new Observer(value, `${path}__${property}`).getData();

        Reflect.set(target, property, target[property]);

        dep.notfiy(`${path}__${property}`, args);

        return true;
      }
    };

    const proxy = new Proxy(data, handler);
    // proxy
    Reflect.defineProperty(data, "__isProxy", {
      value: 1,
      writable: false,
      enumerable: false,
      configurable: false
    });
    return proxy;
  }
  /**
   * 
   *
   * @return {any}
   * @memberof Observer
   */
  public getData(): any {
    return this.data;
  }
}
